@import "~materialize-css/dist/css/materialize.min.css";

.margin-t-20 {
  margin-top: 20px;
}

.margin-r-1 {
  margin-right: 1rem;
}

.margin-l-2 {
  margin-left: 2rem;
}

.center-position {
  display: flex;
  justify-content: center;
  padding-top: 2rem;
}

.pop-up {
  z-index: 12;
  position: absolute;
  display: block;
  background: rgba(0, 0, 0, 0.5);
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
}
